@import "_variable.scss";

/*--------------------------------------------------------------
# Team
--------------------------------------------------------------*/
@charset "UTF-8";
.uta-team{
	ul{
		margin:0;
		padding:0;

		li{
			list-style: none;
		}
	}
	
}

/*Team Default*/
.team-style-default {
	width: 100%;
	overflow: hidden;
	box-shadow: 0px 1px 12px 0px rgba(54, 56, 110, 0.15);
	box-sizing: border-box;
}
.team-style-default img {
	width: 100%;
}
.team-style-default-content {
	text-align: center;
	padding: 24px 10px;
}
.team-style-default-content h3 {
	font-size: 26px;
	letter-spacing: 1px;
	margin-bottom: 5px;
	color: #333;
}
.team-style-default-content h5 {
	font-weight: 400;
	font-size: 18px;
	margin-bottom: 20px;
	margin-top: 6px;
	color: #777;
}
.team-style-default-content .social ul li {
	display: inline-block;
	margin: 0px 2px;
}
.team-style-default-content .social ul li a {
	display: inline-block;
}
.team-style-default-content .social ul li a i {
	color: #333;
	width: 35px;
	height: 35px;
	border: 1px solid #ddd;
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.team-style-default-content .social ul li a i:hover {
	background: $brand-color;
	color: #fff;
	border-color: $brand-color;
}
/*Team Style 1*/
.team-style-1 {
	width: 100%;
	position: relative;
}
.team-style-1 .team-img {
	width: 70%;
	margin: 0 auto;
}
.team-style-1 .team-img img {
	width: 100%;
	border-radius: 40px;
}
.team-content {
	text-align: center;
	padding: 18px 0px;
	background: #f1f7fd;
	margin-top: -132px;
	padding-top: 150px;
	border-radius: 40px;
	padding-bottom: 40px;
	margin-bottom: 20px;
}
.team-content h3 {
	font-size: 28px;
	font-weight: 700;
	margin-bottom: 8px;
	cursor: pointer;
}
.team-content p {
	font-size: 18px;
	text-transform: capitalize;
	letter-spacing: 1px;
	color: #807e7e;
}
.team-overly {
	position: absolute;
	bottom: -41px;
	left: 0;
	right: 0;
	text-align: center;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
}
.team-style-1:hover .team-overly {
	opacity: 1;
	visibility: visible;
	bottom: -20px;
}
.team-overly .team-social ul {
	background: #d71700;
	display: inline-block;
	border-radius: 8px;
	padding: 8px 14px;
}
.team-social ul li {
	display: inline-block;
}
.team-social ul li a {
	display: inline-block;
}
.team-social ul li a i {
	color: #000;
	width: 32px;
	height: 32px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 14px;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	color: #fff;
}
/*Team Style 2*/
.team-style-2 {
	width: 100%;
	overflow: hidden;
	position: relative;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.team-img-2 img {
	width: 100%;
}

.team-style-2-overly {
	position: absolute;
	bottom: 0px;
	left: 0;
	right: 0;
	text-align: center;
	overflow: hidden;
	background: rgba(64, 166, 109, 0.93);
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	border-top-left-radius: 90px;
	border-top-right-radius: 90px;
	padding-top: 25px;
	padding-bottom: 20px;
	opacity: 0;
	visibility: hidden;
	height: 0;
}
.team-style-2:hover .team-style-2-overly {
	opacity: 1;
	visibility: visible;
	height: 160px;
}
.team-style-2-overly-full h3 {
	font-size: 18px;
	color: #fff;
	font-weight: 700;
	margin-bottom: 8px;
}
.team-style-2-overly-full p {
	color: #fff;
	font-size: 12px;
	text-transform: uppercase;
	font-weight: 700;
	letter-spacing: 1.2px;
}
.team-style-2-overly-full .social {
	margin-top: 20px;
	margin-bottom: 20px;
}
.team-style-2-overly-full .social ul li {
	display: inline-block;
	margin: 0px 2px;
}
.team-style-2-overly-full .social ul li a {
	display: inline-block;
}
.team-style-2-overly-full .social ul li a i {
	color: #fff;
	font-size: 13px;
	width: 30px;
	height: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	-webkit-transition: all 0.3s ease-in-out;
	transition:all 0.3s ease-in-out;
	border: 1px solid #d7d7d7;
}
.team-style-2-overly-full .social ul li a i:hover {
	background: #fff;
	color: #333;
}
/*Team 3*/
