/*Team 4*/
.team-style-4 {
	width: 100%;
	overflow: hidden;
	position: relative;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.team-style-4:after {
	content: "";
	clear: both;
	display: block;
	overflow: hidden;
	position: absolute;
	width: 50%;
	height: 0%;
	background:rgba(6, 6, 6, 0.4);
	top: 0;
	right: 0;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.team-style-4:before {
	content: "";
	clear: both;
	display: block;
	overflow: hidden;
	position: absolute;
	width: 50%;
	height: 0%;
	background: rgba(6, 6, 6, 0.4);
	bottom: 0;
	left: 0;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}
.team-style-4:hover::before {
	height: 100%;
}
.team-style-4:hover::after {
	height: 100%;
}
.team-style-single-4-full img {
	width: 100%;
}
.team-style-4-overly {
	position: absolute;
	width: 100%;
	height: 100%;
	overflow: hidden;
	top: 0;
	left: 0;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
	z-index: 999;
}
.team-style-4-overly-full {
	position: absolute;
	left: 0px;
	bottom: 20px;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
}
