	margin: 0px 2px;
}
.team-style-4-overly-full .social ul li a {
	display: inline-block;
}
.team-style-4-overly-full .social ul li a i {
	color: #fff;
	font-size: 13px;
	border: 2px dotted #fff;
	width: 30px;
	height: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	-webkit-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
